.trans {
  background-color: #2487c9;
  padding: 20px 40px;
  border-radius: 5px;
  margin-top: 20px;
  font-size: rem(20px);

  &__form {
    position: relative;

    input {
      background: none;
      border: 0px;
      color: white;
      box-shadow: 0px 1px 0px 0px #ffffff;
      width: 60px;
      margin-inline-start: 5px;
    }

    button {
      margin-inline-start: 5px;
      position: absolute;
      right: 0;
      &.disable {
        background-color: #5579af;
        cursor: not-allowed;
      }
    }
  }
}
@media (max-width: $break-narrow) {
  .trans {
      &__form {
          button {
              position: relative;
          }
      }
  }
}

@media (min-width: $break-normal) {
